'use client';
import React, { useState, useEffect, Suspense } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import {
    FaArrowLeft,
    FaMapMarkerAlt,
    FaMapPin,
    FaGlobe,
    FaBriefcase,
    FaUsers,
    FaEnvelope,
    FaCalendarAlt
} from "react-icons/fa";
import ProtectedRoute from "@/components/ProtectedRoute";
import ProjectRecommender from "@/components/ProjectRecommender";
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

interface CompanyData {
    id: number;
    name: string;
    contact_email?: string;
    website?: string;
    address?: string;
    industry?: string;
    size?: string;
    bio?: string;
    color?: string;
    logo?: string;
    created_at?: string;
    updated_at?: string;
    user_id?: string;
}

function CompanyProfileContent() {
    const API = process.env.NEXT_PUBLIC_API_URL;
    const router = useRouter();
    const searchParams = useSearchParams();

    const [companyData, setCompanyData] = useState<CompanyData | null>(null);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState("");
    const [synergyData, setSynergyData] = useState<any | null>(null);

    const [activeTab, setActiveTab] = useState("all");

    // Get company ID from URL parameters - this is required for employee view
    const companyId = searchParams.get('id');

    const [approvedProposals, setApprovedProposals] = useState<{ id: number; title: string; status: string }[]>([]);
    const [loadingApproved, setLoadingApproved] = useState(false);
    const [approvedError, setApprovedError] = useState<string | null>(null);
    const [pendingProposals, setPendingProposals] = useState<{ id: number; title: string; status: string }[]>([]);
    const [loadingProposals, setLoadingProposals] = useState(false);
    const [proposalError, setProposalError] = useState<string | null>(null);

    const [recommendedProjects, setRecommendedProjects] = useState<{ title: string; description: string }[]>([]);
    const [loadingRecommendations, setLoadingRecommendations] = useState(false);
    const [recommendationsError, setRecommendationsError] = useState<string | null>(null);
    const [isModalOpen, setIsModalOpen] = useState(false);

    const fetchRecommendedProjects = async () => {
        const token = sessionStorage.getItem("access_token");
        if (!token || !companyId) return;

        setLoadingRecommendations(true);
        setRecommendationsError(null);

        try {
            const res = await fetch(`${API}/api/project_recommendation/company/${companyId}`, {
                headers: {
                    Authorization: `Bearer ${token}`,
                    "Content-Type": "application/json",
                },
            });

            if (res.ok) {
                const data = await res.json();

                if (Array.isArray(data) && data.length > 0) {
                    const rec = data[0];

                    const mappedProjects = [
                        { title: rec.title1 || "Untitled Project", description: rec.description1 || "No description available." },
                        { title: rec.title2 || "Untitled Project", description: rec.description2 || "No description available." },
                        { title: rec.title3 || "Untitled Project", description: rec.description3 || "No description available." },
                    ].filter(p => p.title && p.description);

                    setRecommendedProjects(mappedProjects);
                } else {
                    setRecommendedProjects([]); // No data
                }
            } else {
                const errData = await res.json();
                setRecommendationsError(errData.error || "Failed to fetch recommended projects.");
            }
        } catch (error) {
            console.error(error);
            setRecommendationsError("Network error occurred while loading recommendations.");
        } finally {
            setLoadingRecommendations(false);
        }
    };


    useEffect(() => {
        if (companyId) {
            fetchRecommendedProjects();
        }
    }, [companyId]);

    const handleOpenModal = () => {
        setIsModalOpen(true);
    };

    useEffect(() => {
        const token = sessionStorage.getItem("access_token");
        if (!token || !companyId) return;
        console.log(token)
        const fetchProposals = async () => {
            if (activeTab === "pending") {
                setLoadingProposals(true);
                setProposalError(null);
                try {
                    const res = await fetch(`${API}/api/proposal/get_company/${companyId}`, {
                        headers: { Authorization: `Bearer ${token}` }
                    });

                    if (res.ok) {
                        const data = await res.json();
                        setPendingProposals(data.proposals || []);
                    } else {
                        const errData = await res.json();
                        setProposalError(errData.error || "Failed to load proposals");
                    }
                } catch {
                    setProposalError("Network error occurred while loading proposals.");
                } finally {
                    setLoadingProposals(false);
                }
            }

            if (activeTab === "all") {
                setLoadingApproved(true);
                setApprovedError(null);
                try {
                    const res = await fetch(`${API}/api/proposal/get_company_filtered/${companyId}`, {
                        headers: { Authorization: `Bearer ${token}` }
                    });

                    if (res.ok) {
                        const data = await res.json();
                        setApprovedProposals(data.proposals || []);
                    } else if (res.status === 404) {
                        setApprovedProposals([]);
                    } else {
                        const errData = await res.json();
                        setApprovedError(errData.error || "Failed to load approved projects.");
                    }
                } catch {
                    setApprovedError("Network error occurred while loading approved proposals.");
                } finally {
                    setLoadingApproved(false);
                }
            }
        };

        fetchProposals();
    }, [activeTab, API, companyId]);

    const handleFilesClick = (proposalId: number) => {
        router.push(`/bpiproposaltracking?id=${proposalId}`);
    };

    // Convert company size string to display format
    const formatCompanySize = (size?: string): string => {
        if (!size || size === 'N/A') return 'N/A';
        return size;
    };

    const formatScore = (score: number | null | undefined): string => {
        if (score == null) return "0%";
        const percentage = score * 100;
        return Number.isInteger(percentage)
            ? `${percentage}%`
            : `${percentage.toFixed(2)}%`;
    };
    const computeOverallSynergy = (
        credibility: number | null | undefined,
        referential: number | null | undefined,
        compliance: number | null | undefined
    ): number => {
        const scores = [credibility, referential, compliance].filter(
            (s): s is number => s != null
        );

        if (scores.length === 0) return 0;

        const avg = scores.reduce((a, b) => a + b, 0) / scores.length;
        return avg * 100;
    };

    const overallSynergy = computeOverallSynergy(
        synergyData?.credibility_score,
        synergyData?.referential_score,
        synergyData?.compliance_score
    );
    // Fetch specific company data by ID
    useEffect(() => {
        const fetchData = async () => {
            if (!companyId) {
                setError("Company ID is required to view company profile.");
                setIsLoading(false);
                return;
            }

            const token = sessionStorage.getItem("access_token");
            if (!token) {
                setError("Authentication required. Please log in first.");
                setIsLoading(false);
                return;
            }

            try {
                // Fetch company
                const companyRes = await fetch(`${API}/api/company/${companyId}`, {
                    headers: {
                        Authorization: `Bearer ${token}`,
                        "Content-Type": "application/json",
                    },
                });

                if (companyRes.ok) {
                    const data = await companyRes.json();
                    setCompanyData(data.company);
                } else {
                    const errData = await companyRes.json();
                    setError(errData.error || "Failed to load company data");
                }

                // Fetch synergy
                const synergyRes = await fetch(`${API}/api/synergy/company/${companyId}`, {
                    headers: {
                        Authorization: `Bearer ${token}`,
                    },
                });

                if (synergyRes.ok) {
                    const synergy = await synergyRes.json();

                    setSynergyData(synergy);
                } else {
                    console.warn("No synergy data available for this company");
                }
            } catch (err) {
                setError("Network error occurred while loading data.");
            } finally {
                setIsLoading(false);
            }
        };

        fetchData();
    }, [API, companyId]);


    const parseReasonings = (rawText?: string): string[] => {
        if (!rawText) return [];
        return rawText
            .split("•")
            .map(item => item.trim())
            .filter(item => item.length > 0);
    };

    const renderReasonings = (items: string[]) => {
        const shades = ["#B11016", "#60080c"];
        return items.map((text, idx) => (
            <div
                key={idx}
                className="text-white rounded-md p-3 text-sm text-left shadow-md"
                style={{ backgroundColor: shades[idx % shades.length] }}
            >
                {text}
            </div>
        ));
    };


    if (isLoading) {
        return (
            <ProtectedRoute allowedRoles={["employee", "admin"]}>
                <div className="min-h-screen bg-white flex items-center justify-center">
                    <div className="text-center">
                        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#B11016] mx-auto"></div>
                        <p className="mt-4 text-gray-600">Loading company profile...</p>
                    </div>
                </div>
            </ProtectedRoute>
        );
    }

    if (error) {
        return (
            <ProtectedRoute allowedRoles={["employee", "admin"]}>
                <div className="min-h-screen bg-white flex flex-col items-center justify-center px-[10%]">
                    <div className="text-center">
                        <div className="text-red-500 text-6xl mb-4">⚠</div>
                        <h2 className="text-2xl font-bold text-gray-800 mb-2">Error Loading Company</h2>
                        <p className="text-gray-600 mb-6">{error}</p>
                        <button
                            onClick={() => router.back()}
                            className="bg-[#B11016] text-white py-2 px-6 rounded-lg hover:bg-[#800b10] transition-colors"
                        >
                            Go Back
                        </button>
                    </div>
                </div>
            </ProtectedRoute>
        );
    }

    if (!companyData) {
        return (
            <ProtectedRoute allowedRoles={["employee", "admin"]}>
                <div className="min-h-screen bg-white flex flex-col items-center justify-center px-[10%]">
                    <div className="text-center">
                        <FaBriefcase className="text-6xl text-gray-300 mx-auto mb-4" />
                        <h3 className="text-2xl font-bold text-gray-600 mb-2">No Company Data</h3>
                        <p className="text-gray-500 mb-6">Company profile could not be loaded.</p>
                        <button
                            onClick={() => router.back()}
                            className="bg-[#B11016] text-white py-2 px-6 rounded-lg hover:bg-[#800b10] transition-colors"
                        >
                            Go Back
                        </button>
                    </div>
                </div>
            </ProtectedRoute>
        );
    }

    return (
        <ProtectedRoute allowedRoles={["employee", "admin"]}>
            <div className="min-h-screen bg-white flex flex-col items-center px-[10%] py-8">
                {/* Header */}
                <div className="relative flex items-center w-full mt-2 mb-10">
                    {/* Back Button */}
                    <button
                        onClick={() => router.back()}
                        className="absolute left-0 flex items-center text-[#B11016] hover:text-[#800b10]"
                    >
                        <FaArrowLeft className="mr-2" />
                        <span className="hidden sm:inline">Back to Company List</span>
                    </button>


                </div>

                {/* Company Profile Display */}
                <div className="w-full max-w-8xl mx-auto">
                    {/* Company Logo */}
                    <div className="text-center mb-8">
                        {companyData.logo ? (
                            <div className="inline-block">
                                <img
                                    src={companyData.logo}
                                    alt={`${companyData.name} Logo`}
                                    className="max-w-48 max-h-48 mx-auto rounded-lg shadow-lg"
                                />
                            </div>
                        ) : (
                            <div className="w-48 h-48 mx-auto bg-gray-200 rounded-lg flex items-center justify-center shadow-lg">
                                <span className="text-gray-500 text-lg font-medium">No Logo</span>
                            </div>
                        )}
                    </div>

                    {/* Company Name */}
                    <div className="text-center mb-8">
                        <h2
                            className="text-3xl sm:text-5xl font-bold mb-4 text-[#B11016]"
                        // style={{ color: companyData.color || '#B11016' }}
                        >
                            {companyData.name}
                        </h2>
                        {companyData.bio && (
                            <p className="text-gray-600 text-lg max-w-2xl mx-auto leading-relaxed">
                                {companyData.bio}
                            </p>
                        )}
                    </div>

                    {/* Company Details Row */}
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-6 gap-6 mb-8 border-b-2 border-gray-800 pb-4">
                        {/* Location */}
                        {companyData.address && (
                            <div className="flex items-center gap-3 p-4 transition-colors">
                                <FaMapMarkerAlt
                                    className="text-2xl"
                                    style={{ color: '#B11016' }}
                                />
                                <div>
                                    <h3 className="font-semibold text-gray-800">Location</h3>
                                    <p className="text-gray-600 text-sm">{companyData.address}</p>
                                </div>
                            </div>
                        )}

                        {/* Website */}
                        {companyData.website && (
                            <div className="flex items-center gap-3 p-4 transition-colors">
                                <FaGlobe
                                    className="text-2xl"
                                    style={{ color: '#B11016' }}
                                />
                                <div>
                                    <h3 className="font-semibold text-gray-800">Website</h3>
                                    <a
                                        href={companyData.website}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                        className="text-sm hover:underline break-all"
                                        style={{ color: '#B11016' }}
                                    >
                                        {companyData.website.replace(/^https?:\/\//, '')}
                                    </a>
                                </div>
                            </div>
                        )}

                        {/* Industry */}
                        {companyData.industry && (
                            <div className="flex items-center gap-3 p-4 transition-colors">
                                <FaBriefcase
                                    className="text-2xl"
                                    style={{ color: '#B11016' }}
                                />
                                <div>
                                    <h3 className="font-semibold text-gray-800">Industry</h3>
                                    <p className="text-gray-600 text-sm">{companyData.industry}</p>
                                </div>
                            </div>
                        )}

                        {/* Company Size */}
                        {companyData.size && (
                            <div className="flex items-center gap-3 p-4 transition-colors">
                                <FaUsers
                                    className="text-2xl"
                                    style={{ color: '#B11016' }}
                                />
                                <div>
                                    <h3 className="font-semibold text-gray-800">Team Size</h3>
                                    <p className="text-gray-600 text-sm">{formatCompanySize(companyData.size)}</p>
                                </div>
                            </div>
                        )}

                        {/* Contact Email */}
                        {companyData.contact_email && (
                            <div className="flex items-center gap-3 p-4 transition-colors">
                                <FaEnvelope
                                    className="text-2xl"
                                    style={{ color: '#B11016' }}
                                />
                                <div>
                                    <h3 className="font-semibold text-gray-800">Contact Email</h3>
                                    <a
                                        href={`mailto:${companyData.contact_email}`}
                                        className="text-sm hover:underline break-all"
                                        style={{ color: '#B11016' }}
                                    >
                                        {companyData.contact_email}
                                    </a>
                                </div>
                            </div>
                        )}

                        {/* Creation Date */}
                        {companyData.created_at && (
                            <div className="flex items-center gap-3 p-4 transition-colors">
                                <FaCalendarAlt
                                    className="text-2xl"
                                    style={{ color: '#B11016' }}
                                />
                                <div>
                                    <h3 className="font-semibold text-gray-800">Registered Since</h3>
                                    <p className="text-gray-600 text-sm">
                                        {new Date(companyData.created_at).toLocaleDateString("en-US", {
                                            year: "numeric",
                                            month: "long",
                                            day: "numeric",
                                        })}
                                    </p>
                                </div>
                            </div>
                        )}
                    </div>

                    {/* Synergy Score */}
                    {synergyData && (
                        <div className="text-center my-12 border-b-2 border-gray-800 pb-10">
                            <h3 className="text-2xl font-bold text-red-700 mb-6">Synergy Score</h3>

                            {/* Circular Progress */}
                            <div className="w-48 h-48 mx-auto mb-12">
                                <CircularProgressbar
                                    value={overallSynergy}
                                    text={`${formatScore(overallSynergy / 100)}`}
                                    styles={buildStyles({
                                        textSize: "20px",
                                        textColor: "#111827",
                                        pathColor: "#B11016",
                                        trailColor: "#d1d5db",
                                    })}
                                />
                            </div>

                            {/* Categories Row */}
                            <div className="grid grid-cols-1 sm:grid-cols-3 gap-10 max-w-6xl mx-auto text-center">

                                {/* Credibility */}
                                <div>
                                    <h4 className="text-xl font-bold text-red-700 mb-2">Credibility</h4>
                                    <p className="text-4xl font-bold mb-4">{formatScore(synergyData.credibility_score)}</p>
                                    <div className="border-t-2 border-gray-800 mb-8"></div>
                                    <div className="space-y-6">
                                        {renderReasonings(parseReasonings(synergyData.credibility_reasonings))}
                                    </div>
                                </div>

                                {/* Referential */}
                                <div>
                                    <h4 className="text-xl font-bold text-red-700 mb-2">Referential</h4>
                                    <p className="text-4xl font-bold mb-4">{formatScore(synergyData.referential_score)}</p>
                                    <div className="border-t-2 border-gray-800 mb-8"></div>
                                    <div className="space-y-6">
                                        {renderReasonings(parseReasonings(synergyData.referential_reasonings))}
                                    </div>
                                </div>

                                {/* Compliance */}
                                <div>
                                    <h4 className="text-xl font-bold text-red-700 mb-2">Compliance</h4>
                                    <p className="text-4xl font-bold mb-4">{formatScore(synergyData.compliance_score)}</p>
                                    <div className="border-t-2 border-gray-800 mb-8"></div>
                                    <div className="space-y-6">
                                        {renderReasonings(parseReasonings(synergyData.compliance_reasonings))}
                                    </div>
                                </div>

                            </div>
                        </div>
                    )}

                    <div className="w-full px-[10%] justify-center">
                        <div className="flex w-full border-b mb-4">
                            <button
                                onClick={() => setActiveTab("pending")}
                                className={`flex-1 text-center py-2 font-semibold transition ${activeTab === "pending"
                                    ? "bg-[#B11016] text-white rounded-t"
                                    : "text-gray-600 hover:text-black"
                                    }`}
                            >
                                Proposals Submitted
                            </button>
                            <button
                                onClick={() => setActiveTab("all")}
                                className={`flex-1 text-center py-2 font-semibold transition ${activeTab === "all"
                                    ? "bg-[#B11016] text-white rounded-t"
                                    : "text-gray-600 hover:text-black"
                                    }`}
                            >
                                Projects Together
                            </button>
                        </div>

                        {/* Table */}
                        <div className="w-full bg-white shadow rounded-lg p-4 overflow-x-auto border border-gray-400">
                            {activeTab === "pending" ? (
                                loadingProposals ? (
                                    <p className="text-center">Loading proposals...</p>
                                ) : proposalError ? (
                                    <p className="text-center">{proposalError}</p>
                                ) : (
                                    <FileTable
                                        files={pendingProposals}
                                        onRowClick={handleFilesClick}
                                        emptyMessage="No proposals submitted yet."
                                    />
                                )
                            ) : (
                                loadingApproved ? (
                                    <p className="text-center">Loading approved projects...</p>
                                ) : approvedError ? (
                                    <p className="text-center">{approvedError}</p>
                                ) : (
                                    <FileTable
                                        files={approvedProposals}
                                        onRowClick={handleFilesClick}
                                        emptyMessage="No approved projects yet."
                                    />
                                )
                            )}
                        </div>

                        {/* Centered Button */}
                        <div className="flex justify-center mt-4">
                            <button
                                className="min-w-2xl cursor-pointer font-bold text-center py-4 px-6 text-white bg-[#B11016] border-2 border-[#B11016] rounded-md hover:text-[#B11016] hover:bg-white hover:border-[#B11016] transition-all duration-300 ease-in-out transform hover:scale-105 text-base sm:text-lg"
                                onClick={handleOpenModal}
                            >
                                Get Project Ideas
                            </button>
                        </div>
                    </div>



                </div>
            </div>
            <ProjectRecommender
                isOpen={isModalOpen}
                onClose={() => setIsModalOpen(false)}
                projects={recommendedProjects}
            />
        </ProtectedRoute>
    );
}

export default function CompanyProfile() {
    return (
        <Suspense fallback={
            <div className="flex justify-center items-center min-h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
            </div>
        }>
            <ProtectedRoute allowedRoles={["employee", "admin"]}>
                <CompanyProfileContent />
            </ProtectedRoute>
        </Suspense>
    );
}

const FileTable = ({
    files,
    emptyMessage,
    onRowClick,
}: {
    files: { id: number; title: string; status: string }[];
    emptyMessage: string;
    onRowClick: (id: number) => void;
}) => (
    <table className="w-full text-sm rounded-lg overflow-hidden min-w-[600px]">
        <thead>
            <tr>
                <th className="p-3 text-left text-red-700 whitespace-nowrap">Proposal ID</th>
                <th className="p-3 text-left text-red-700 whitespace-nowrap">Proposal Title</th>
                <th className="p-3 text-left text-red-700 whitespace-nowrap">Status</th>
                <th className="p-3 text-center text-red-700 whitespace-nowrap">Action</th>
            </tr>
        </thead>
        <tbody>
            {files.length > 0 ? (
                files.map((file, i) => (
                    <tr
                        key={i}
                        className="border-t hover:bg-gray-100 transition cursor-pointer"
                        onClick={() => onRowClick(file.id)}
                    >
                        <td className="p-3">{file.id}</td>
                        <td className="p-3">{file.title}</td>
                        <td className="p-3">{file.status}</td>
                        <td className="p-3 text-center">
                            <div className="flex items-center justify-center">
                                <svg
                                    className="w-4 h-4 text-gray-400"
                                    fill="none"
                                    stroke="currentColor"
                                    viewBox="0 0 24 24"
                                >
                                    <path
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                        strokeWidth={2}
                                        d="M9 5l7 7-7 7"
                                    />
                                </svg>
                            </div>
                        </td>
                    </tr>
                ))
            ) : (
                <tr>
                    <td colSpan={4} className="text-center py-4">
                        {emptyMessage}
                    </td>
                </tr>
            )}
        </tbody>
    </table>
);